import React from "react";
import { Box, Avatar, ScaleFade, Heading, Text, Flex } from "@chakra-ui/react";

const About: React.FC = () => {
  return (
    <Box textAlign="left" mt="100px">
      <Heading
        size="md"
        mb={6}
        mx="auto"
        textAlign={{ base: "center", sm: "left" }}
      >
        自己紹介
      </Heading>
      <ScaleFade initialScale={0.5} in={true}>
        <Flex
          flexDirection={{ base: "column", sm: "row" }}
          alignItems={{ base: "center", sm: "center" }}
        >
          <Avatar
            src={process.env.PUBLIC_URL + "/images/icons/hiiragi_logo.png"}
            size="xl"
            mr={{ base: 0, sm: 10 }}
          />
          <Box mt={{ base: 4, sm: 0 }}>
            <Heading
              mt={2}
              size="md"
              textAlign={{ base: "center", sm: "left" }}
            >
              柊 琴音
            </Heading>
            <Heading
              mt={1}
              mb={4}
              size="xs"
              fontWeight="none"
              textAlign={{ base: "center", sm: "left" }}
            >
              Hiiragi Kotone
            </Heading>
            <Heading
              size="xs"
              fontWeight="none"
              textAlign={{ base: "center", sm: "left" }}
            >
              トヨタ自動車に期間工として3年間勤務。
            </Heading>
            <Heading
              size="xs"
              fontWeight="none"
              textAlign={{ base: "center", sm: "left" }}
            >
              その後、趣味であるプログラミングを行うためIT企業に転職。
            </Heading>
            <Heading
              size="xs"
              fontWeight="none"
              textAlign={{ base: "center", sm: "left" }}
            >
              エンジニアとして多くのプロジェクトに携わる中でもっと柔軟な働き方をしたいと感じ、辞職しました。
            </Heading>
            <Heading
              mt={1}
              size="xs"
              fontWeight="none"
              textAlign={{ base: "center", sm: "left" }}
            >
              現在は個人としてWeb系開発、ドット絵制作を行っております。
            </Heading>
          </Box>
        </Flex>
      </ScaleFade>
    </Box>
  );
};

export default About;
